export default function Main() {
    const [base, setBase] = useState("#ffffff");
    const [transparent, setTransparent] = useState(false);
    const [accent, setAccent] = useState("#ab149e");
    const [large, setLarge] = useState(true);
    const [bg, setBg] = useState("#000000");

    // fetch the params from the url
    // otherwise use the standard values
    // useEffect(() =>{
    //     const initial = parseParams(params);
    //     setBase(initial.base);
    //     setAccent(initial.accent);
    //     setBg(initial.bg);
    //     setTransparent(initial.transparent);
    //     setLarge(initial.large);
    // }, []);

    const loaded = [base, transparent, accent, large, bg].every(x => x != null);
    
    // Update the url
    // const browserHistory = createBrowserHistory();
    // if (loaded) {
    //     browserHistory.replace(
    //         "/" + base.substring(1) + 
    //         "/" + accent.substring(1) + 
    //         "/" + bg.substring(1) + 
    //         "/" + (transparent ? "1" : "0") + 
    //         "/" + (large? "1" : "0"));
    // }
    

    return loaded && <>
        <Logo
            base={base}
            accent={accent}
            background={bg}
            transparent={transparent}
            large={large}
        />
        <Controls
            base={base}
            setBase={setBase}
            accent={accent}
            setAccent={setAccent}
            bg={bg}
            setBg={setBg}
            changeTransparency = {() => setTransparent(!transparent)}
            changeSize={() => setLarge(!large)}
        />
    </>;
}