import React from 'react';
import {large_logo, small_logo} from './LogoContent.js'
export default function Logo(props) {
    let genLogo = props.large ? large_logo : small_logo
    
    return <div 
                className="Logo"
                dangerouslySetInnerHTML={{__html: genLogo(  props.accent, 
                                                            props.base,
                                                            props.background,
                                                            props.transparent)
                                                        }}
            ></div>;
}