import React from 'react';
import { CompactPicker } from 'react-color';

function ColorPicker(props) {
    return <>
        <h2>{props.title}</h2>
        <span className="desc">{props.desc}</span>
        <CompactPicker
                color={props.color}
                onChangeComplete={(color) => {props.set(color.hex);}}
        ></CompactPicker>
    </>;
}

function Button(props) {
    return <div onClick={props.action} className="Button">
        <span>{props.children}</span>
    </div>;
}

export default function Controls(props) {
    
    return <div className="Controls">
        <div className="options">
            <h1>UAPFF <span style={{fontWeight:"normal"}}>Studio</span></h1>
            <ul>
                <li>
                    <Button action={props.changeSize}>
                        Med/utan text
                    </Button>
                </li>
                <li>
                    <Button action={props.changeTransparency}>
                        Visa/dölj bakgrund
                    </Button>
                </li>
                <li>
                    <ColorPicker
                        title="Bakgrundsfärg"
                        color={props.bg}
                        set={props.setBg}
                    />
                </li>
                <li>
                    <ColorPicker
                        title="Accentfärg"
                        color={props.accent}
                        set={props.setAccent}
                    />
                </li>
                <li>
                    <ColorPicker
                        title="Basfärg"
                        desc="text + cirkel"
                        color={props.base}
                        set={props.setBase}
                    />
                </li>
                
            </ul>
        </div>
    </div>;
}